/* Используем переменную --tg-vh для корректного расчета высоты */
html {
  --tg-vh: 1vh; /* Значение по умолчанию */
}

body {
  font-family: Arial, sans-serif;
  background-color: #f0f2f5;
  padding: 16px;
  min-height: calc(var(--tg-vh) * 100); /* Используем --tg-vh для высоты */
  display: flex;
  flex-direction: column;
  margin: 0;
}

#root {
  flex: 1;
  overflow-y: auto;
  padding-bottom: env(safe-area-inset-bottom); /* Учитываем безопасную область */
  transition: padding 0.3s ease; /* Плавный переход при изменении отступа */
}

input {
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 8px;
  font-size: 16px;
  width: 100%;
  box-sizing: border-box;
}

input:focus {
  outline: none;
  border-color: #007bff;
}

label {
  display: block;
  margin-bottom: 10px;
}

h1 {
  text-align: center;
  margin-bottom: 20px;
}

@media (max-width: 480px) {
  body {
    padding: 8px;
  }

  input {
    font-size: 14px;
    padding: 6px;
  }
}
